dialog{
  z-index: 500;
}
dialog::-webkit-backdrop {
    background-color: rgba(0, 0, 0, 0.5);
}
dialog::backdrop {
    background-color: rgba(0, 0, 0, 0.5);
}
.Backdop{
  width: 100%;
  height: 100%;
  z-index: 100;
  position: fixed;
  top: 0;
  left: 0;
}


